.time-row--additional {
  display: flex;
}

.delete-calendar-row {
  visibility: collapse;
}

.time-row--additional .delete-calendar-row {
  visibility: visible;
}

.time-row--additional.js-hidden,
.time-row--additional.hidden {
  display: none;
}

.calendar-day {
  flex-direction: column;
  align-items: baseline;
  width: 100%;
}
@media (min-width: 768px) {
  .calendar-day {
    flex-direction: row;
  }
}

.time_rows {
  display: none;
  width: 100%;
}

.calendar__checkbox--day:checked ~ .time_rows {
  display: flex;
}

.time-row-desc {
  display: flex;
  min-height: 56px;
}

.calendar__checkbox--day:checked ~ .time-row-desc {
  display: none;
}

.calendar__checkbox--day {
  display: none;
}

.calendar__break-row {
  margin-top: 15px;
  width: 100%;
  display: none;
}
@media (min-width: 768px) {
  .calendar__break-row {
    margin-top: 0px;
    width: auto;
  }
}

.calendar__checkbox--day:checked ~ .calendar__break-row {
  display: flex;
}

.calendar__button--delete-time-slot {
  cursor: pointer;
}

.calendar__button--time-break {
  text-decoration: none;
  color: #4285f4;
  cursor: pointer;
  user-select: none;
  font-size: 0.875rem;
}

.calendar__button--time-break.js-disabled {
  color: #dddddd;
  cursor: default;
  pointer-events: none;
}

.calendar__button--time-break:hover {
  color: #001487;
}

.time_rows {
  align-items: start;
}

.time-row--clock-icon {
  width: 30px;
  height: 30px;
  min-width: 30px;
  background-image: url("/static/images/clock.svg");
  background-size: contain;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .time-row--clock-icon {
    width: 20px;
    height: 20px;
    min-width: 20px;
  }
}

.time-row.additional .time-row--clock-icon {
  background-image: url("/static/images/clock-additional.svg");
}

.time-row--clock-icon_wrap {
  width: 100%;
  justify-items: start;
  margin-bottom: 10px;
}
@media (min-width: 768px) {
  .time-row--clock-icon_wrap {
    width: unset;
    margin-bottom: 0px;
  }
}

.time-row--delete-button_wrap {
  width: 100%;
  justify-items: end;
  margin-top: 10px;
}
@media (min-width: 768px) {
  .time-row--delete-button_wrap {
    width: unset;
    margin-top: 0px;
  }
}

.signup__thumbnail {
  display: none;
}

.js-hidden {
  display: none;
}

.meeting-duration--icon {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}

.appointment-list__header {
  padding-left: 20px;
}

.subsection__header-scheduled {
  margin-left: 20px;
  color: #e5332a;
}

div.list-link--accept,
div.list-link--reject {
  width: unset;
  min-width: unset;
  max-width: unset;
  border-radius: 30px;
  padding: 5px 10px 2px 10px;
  font-size: 11px;
  font-weight: 500;
}

div.list-link--accept {
  border-color: #00a100;
}

div.list-link--reject {
  border-color: #e5332a;
}

div.list-link--accept:hover {
  background-color: #00a100;
  color: white;
}

div.list-link--reject:hover {
  background-color: #e5332a;
  color: white;
}

.meeting-time--icon {
  width: 25px;
  height: 25px;
  min-width: 25px;
  margin-right: 5px;
}

.widget-list__field--date-time {
  width: 160px;
  display: none;
}
@media (min-width: 576px) {
  .widget-list__field--date-time {
    display: flex;
  }
}

.widget-list__field--status-icon svg {
  width: 30px;
  min-width: 30px;
  height: 30px;
}

.mdc-linear-progress--page {
  position: absolute;
}

.intl-tel-input.separate-dial-code .selected-flag {
  background: transparent !important;
}

.intl-tel-input input {
  z-index: 1 !important;
}

.intl-tel-input .flag-container {
  z-index: 2;
}

.mdc-snackbar {
  z-index: 6;
}

.mdc-snackbar__text {
  color: #00a100;
}

.mdc-snackbar__action-button {
  color: white;
}

.mdc-list-item a {
  color: #001588;
  text-decoration: none;
  font-weight: 600;
}

.mdc-radio .mdc-radio__native-control:enabled:not(:checked) + .mdc-radio__background .mdc-radio__outer-circle {
  border-color: transparent;
}

.mdc-radio .mdc-radio__native-control:enabled:checked + .mdc-radio__background .mdc-radio__outer-circle {
  border-color: #001588;
  border-width: 2px;
}

.mdc-radio__outer-circle,
.mdc-radio__inner-circle {
  border-radius: 0;
}