div.loader,
div.loader:after {
  border-radius: 50%;
  width: 48px;
  height: 48px;
}

div.loader {
  z-index: 1;
  margin: auto;
  font-size: 0.25rem;
  position: fixed;
  top: calc(50% - 24px);
  left: calc(50% - 24px);
  text-indent: -9999em;
  border-right: 0.3em solid #80d100;
  border-bottom: 0.3em solid #001588;
  border-top: 0.3em solid #001588;
  border-left: 0.3em solid #80d100;
  transform: translateZ(0);
  animation: load8 1.1s infinite linear;
}

div.loader.loader--inbox {
  position: absolute;
}

@-webkit-keyframes load8 {
  0% {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(1turn);
  }
}

@keyframes load8 {
  0% {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(1turn);
  }
}

