  line-height: 32px;
}

.clr-field {
  // width: 30px;
  //height: 30px;
}

.color-button {
}

.color-dot {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  position: relative;
  box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.05);
  cursor: pointer;
}

.color-dot:hover {
  opacity: 0.7;
}

.color-dot-wrap {
  position: relative;
}

.calendar-wrap {
  margin: 0 auto;
}

.calendar-day-label {
  position: relative;
  text-transform: uppercase;
  color: #dddddd;
  font-weight: 700;
  margin-left: 15px;
  cursor: pointer;
  width: 160px;
  margin-bottom: 15px;
  padding-left: 30px;

  @media (min-width: $s-md) {
    margin-bottom: 0px;
  }
}

.calendar-day-label::before {
  content: '';
  position: absolute;
  width: 15px;
  height: 15px;
  min-width: 15px;
  background-image: url('/static/images/checkbox.svg');
  background-size: contain;
  background-repeat: no-repeat;
  top: 2px;
  left: 0;
}

.calendar__checkbox--day:checked ~ .calendar-day-label::before {
  color: #001487;
  background-image: url('/static/images/checkbox-checked.svg');
}

.calendar__checkbox--day:checked ~ .calendar-day-label:hover,
.calendar-day-label:hover {
  color: #4285f4;
}

.calendar__checkbox--day:checked ~ .calendar-day-label {
  color: #001487;
}

.calendar__day-slot {
  box-shadow: 0 0 0 4px rgba(0, 0, 0, 0.05);
  padding: 15px;
  margin: 25px;
  border-radius: 15px;
  width: 100%;
  min-height: 105px;
}

.calendar__day-slot:hover {
  background-color: #fefefe;
}

.calendar_to_between {
  margin: 15px 0px 15px 0px;

  @media (min-width: $s-md) {
    margin: 0px 15px 0px 15px;
  }
}

select.time_select {
  -webkit-appearance: initial !important;
  appearance: initial !important;

  background-color: white;
  border-color: #efefef;
  border-width: 1px;
  border-radius: 5px;
  border-style: solid;
  margin-right: 8px;
  margin-left: 8px;
  padding: 8px;
  padding-right: 18px;

  cursor: pointer;

  background-image: url('/static/images/arrow-down.svg');
  background-size: 10px;
  background-position: calc(100% - 5px) 50%;
  background-repeat: no-repeat;
  text-indent: 0;
}

.calendar__checkbox--day {
  cursor: pointer;
}

.calendar__select--hour,
.calendar__select--minutes {
  background-color: #f1f6ff !important;
}

.time-row {
  flex-direction: column;
  width: 100%;
  border-radius: 10px;
  border: 1px solid #efefef;
  padding: 5px;
  margin-bottom: 10px;

  @media (min-width: $s-md) {
    width: unset;
    flex-direction: row;
  }
}

.time-row--additional {
  display: flex;
}

.delete-calendar-row {
  visibility: collapse;
}

.time-row--additional .delete-calendar-row {
  visibility: visible;
}

.time-row--additional.js-hidden,
.time-row--additional.hidden {
  display: none;
}

.calendar-day {
  flex-direction: column;
  align-items: baseline;
  width: 100%;

  @media (min-width: $s-md) {
    flex-direction: row;
  }
}

.time_rows {
  display: none;
  width: 100%;
}

.calendar__checkbox--day:checked ~ .time_rows {
  display: flex;
}

.time-row-desc {
  display: flex;
  min-height: 56px;
}

.calendar__checkbox--day:checked ~ .time-row-desc {
  display: none;
}

.calendar__checkbox--day {
  display: none;
}

.calendar__break-row {
  margin-top: 15px;
  width: 100%;
  display: none;

  @media (min-width: $s-md) {
    margin-top: 0px;
    width: auto;
  }
}

.calendar__checkbox--day:checked ~ .calendar__break-row {
  display: flex;
}

.calendar__button--delete-time-slot {
  cursor: pointer;
}

.calendar__button--time-break {
  text-decoration: none;
  color: #4285f4;
  cursor: pointer;
  user-select: none;
  font-size: 0.875rem;
}

.calendar__button--time-break.js-disabled {
  color: #dddddd;
  cursor: default;
  pointer-events: none;
}

.calendar__button--time-break:hover {
  color: #001487;
}

.time_rows {
  align-items: start;
}

.time-row--clock-icon {
  width: 30px;
  height: 30px;
  min-width: 30px;
  background-image: url('/static/images/clock.svg');
  background-size: contain;
  background-repeat: no-repeat;

  @media (min-width: $s-md) {
    width: 20px;
    height: 20px;
    min-width: 20px;
  }
}

.time-row.additional .time-row--clock-icon {
  background-image: url('/static/images/clock-additional.svg');
}

.time-row--clock-icon_wrap {
  width: 100%;
  justify-items: start;
  margin-bottom: 10px;

  @media (min-width: $s-md) {
    width: unset;
    margin-bottom: 0px;
  }
}

.time-row--delete-button_wrap {
  width: 100%;
  justify-items: end;
  margin-top: 10px;

  @media (min-width: $s-md) {
    width: unset;
    margin-top: 0px;
  }
}

.signup__thumbnail {
  display: none;
}

.js-hidden {
  display: none;
}

.meeting-duration--icon {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}

.appointment-list__header {
  padding-left: 20px;
}

.subsection__header-scheduled {
  margin-left: 20px;
  color: #e5332a;
}

div.list-link--accept,
div.list-link--reject {
  width: unset;
  min-width: unset;
  max-width: unset;
  border-radius: 30px;
  padding: 5px 10px 2px 10px;
  font-size: 11px;
  font-weight: 500;
}

div.list-link--accept {
  border-color: #00a100;
}

div.list-link--reject {
  border-color: #e5332a;
}

div.list-link--accept:hover {
  background-color: #00a100;
  color: white;
}

div.list-link--reject:hover {
  background-color: #e5332a;
  color: white;
}

.meeting-time--icon {
  width: 25px;
  height: 25px;
  min-width: 25px;
  margin-right: 5px;
}

.widget-list__field--date-time {
  width: 160px;
  display: none;
  @media (min-width: $s-sm) {
    display: flex;
  }
}

.widget-list__field--status-icon svg {
  width: 30px;
  min-width: 30px;
  height: 30px;
}
